body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #AFFFAF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* HEADER */

header{
  display: flex;
  align-items: left;
  flex-direction: column;
  text-align: left;
  border-bottom: 3px solid white;
}
.header__title{
  margin: 0;
  color: #00AF78;
  font-size: 40px;
  margin: 10px;
}


/* POSTS */
.posts__container{
  display: flex;
  flex-direction: column;
  max-width: 800px;
  height: auto;
  margin: 10px;
  word-break: break-word;
  border-bottom: 1px black solid;
}
.posts__container p{
  font-size: 16px;
  margin: 5px;
} 
.posts__container h3{
  margin: 0;
  font-size: 20px;
}
.posts__container h6{
  margin: 5px;
  font-size: 14px;
  text-decoration: underline;
}
.submit__button{
  outline: none;
  background-color: transparent;
  height: 50px;
  width: 200px;
  border-radius: 30px;
  font-size: 18px;
  border: 2px solid black;
  cursor: pointer;
  transition: 500ms;
}
.submit__button:hover{
  background-color: rgba(0, 170, 73, 0.8);
  transition: 500ms;
}
.editing_input{
  font-size: 16px;
  margin: 12px 0 12px 0;
  outline: none;
  background-color: rgba(210, 255, 210,0.8);
  border: 0;
  border-bottom: 1px solid black;
  height: 30px;
  width: 550px;
}
.editing_input-button{
  width: 100px;
  margin: 0 0 12px 0;
  outline: none;
  background-color: transparent;
  height: 50px;
  border-radius: 30px;
  font-size: 20px;
  border: 2px solid black;
  cursor: pointer;
  transition: 500ms;
  
}
.editing_input-button:hover{
  background-color: rgba(0, 170, 73, 0.8);
  transition: 500ms;
}
.editing_container{
  display: flex;
  flex-direction: column;
}
.editing-label{
  font-weight: 800;
  text-decoration: underline;
}